import { useState } from 'react'

const AuthModal = ({ setShowModal, isSignUp}) => {

    const [ email, setEmail] = useState(null)
    const [ password, setPassword] = useState(null)
    const [ confirmPassword, setConfirmPassword] = useState(null)
    const [ error, setError] = useState(null)


    console.log(email, password, confirmPassword)


    const handleClick = () => {
        setShowModal(false)
    }

    const handleSubmit = (e) => {
        e.preventDefault()

        try {
            if( isSignUp && ( password !== confirmPassword)) {
                setError('Passwords do not match')
            }
            console.log ('make a post request to sign up')
        } catch (error) {
            console.log(error)
        }
    }
    
    
    return (
        <div className="auth-modal">
        <div className = "close-icon" onClick = {handleClick}>(#)</div>
        <h2>{isSignUp ? 'Create Account' : 'LOG IN'}</h2>
        <p>By clicking the button, you agree to our terms and conditions, use of data in our privacy policy</p>
        <form onSubmit ={handleSubmit}>
            <input
                type ="email"
                name = "email"
                id ="email"
                placeholder="email"
                required={true}
                onChange= {(e) => setEmail(e.target.value)}
            />

            <input
                type ="password"
                name = "password"
                id ="password"
                placeholder="password"
                required={true}
                onChange= {(e) => setEmail(e.target.value)}
            />

            {isSignUp && <input
                type ="password"
                name = "password-check"
                id ="password-check"
                placeholder="password-check"
                required={true}
                onChange= {(e) => setEmail(e.target.value)}
            />}

            
            <input className="secondary-button" type ="submit"/>
                <p>{error}</p> 
               
        </form>
        <hr/>
        <h2>Get the App here</h2>
        AUTH MODAL
        </div>
    )
}

export default AuthModal;